<template>
  <el-form :inline="true" class="demo-form-inline" style="margin-top: 10px">
    <el-row>
      <el-col :span="5">
        <el-form-item label="月份:" prop="username">
          <div class="block">
            <el-date-picker
              type="month"
              size="mini"
              style="width: 150px"
              v-model="date"
              value-format="yyyy-MM"
              format="yyyy年MM月"
              placeholder="选择月"
            >
            </el-date-picker>
          </div>
        </el-form-item>
      </el-col>
      <el-col :span="5">
        <el-form-item>
          <el-button size="mini" icon="el-icon-search" type="primary" @click="generalSalaryReport">查询</el-button>
          <el-button size="mini" icon="el-icon-upload2" type="primary" @click="exportExcelFile">导出Excel</el-button>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-table
        :data="salaryList"
        border
        height="650"
        style="width: 100%;">

        <el-table-column
          align="center"
          type="index"
          fixed="left"
          width="100"
        >
        </el-table-column>
        <el-table-column
          prop="patientCode"
          align="center"
          fixed
          label="病人编号"
        ></el-table-column>
        <el-table-column
          prop="name"
          align="center"
          fixed
          label="姓名"
        >
        </el-table-column>
        <el-table-column
          prop="hospitailDate"
          align="center"
          label="入院时间"
        ></el-table-column>
        <el-table-column
          prop="org"
          align="center"
          label="科室"
        >
        </el-table-column>
        <el-table-column
          prop="bedNum"
          align="center"
          label="床号">
        </el-table-column>
        <el-table-column
          prop="pay"
          label="预收款"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="careType"
          align="center"
          label="陪护类型"
        ></el-table-column>
        <el-table-column
          prop="price"
          align="center"
          label="单价"
        ></el-table-column>
        <el-table-column
          prop="status"
          align="center"
          label="服务状态"
        ></el-table-column>
        <el-table-column
          prop="leaveDate"
          align="center"
          label="出院日期"
        ></el-table-column>
        <el-table-column
          prop="day"
          align="center"
          label="天数"
        ></el-table-column>
        <el-table-column
          prop="amount"
          align="center"
          label="服务金额"
        ></el-table-column>
      </el-table>
    </el-row>

  </el-form>
</template>

<script>

  export default {
    data() {
      return {
        salaryList: [],
        mergeArr: [],
        pos: 0,
        date: ''
      };
    },
    methods: {


      generalSalaryReport() {

        const loading = this.$loading({
          lock: true,
          text: '数据加载中，请等待...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });

        this.postRequestJson('/report/patient', {
          projectId: 2,
          date: this.date

        }).then(resp => {
          console.log(resp);
          loading.close();
          this.salaryList = [];


          if (!(resp && resp.data.code === '200')) {
            this.$message({
              type: 'fail',
              message: resp.data.msg
            });
            return;
          }

          let result = resp.data.msg;

          if (result.length === 0) {
            this.$message({
              type: 'fail',
              message: "该月份没有数据"
            });
            return;
          }


          for (let i = 0; i < result.length; i++) {
            this.salaryList.push({
              patientCode: result[i].patientCode,
              hospitailDate: result[i].hospitalDate,
              name: result[i].patientName,
              org: result[i].orgName,
              bedNum: result[i].bedNum,
              pay: result[i].pay,
              careType: result[i].careTypeName,
              price: result[i].price,
              status: result[i].status,
              leaveDate: result[i].leaveDate,
              day: result[i].days,
              amount: result[i].amount
            });
          }
        })

      },
      exportExcelFile() {
        const loading = this.$loading({
          lock: true,
          text: '数据加载中，请等待...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        this.postRequestFile('/report/patient/file', {
          projectId: 2,
          date: this.date
        }).then(resp => {
          console.log(resp);
          loading.close();
          if (resp && resp.status == 200 && resp.data.code == null) {
            let blob = new Blob([resp.data], {type: 'application/vnd.ms-excel'})
            let link = document.createElement('a')
            link.href = window.URL.createObjectURL(blob)
            //配置下载的文件名
            link.download = '住院报表' + '_' + this.date + '.xlsx'
            link.click()
          } else if (resp.data.code != null) {
            this.$message({
              type: 'fail',
              message: resp.data.msg
            });
            return;
          }

        });


      },
    }
  }

</script>

<style scoped>

</style>



