<template>
 <div>
   <el-form :inline="true" class="demo-form-inline" style="margin-top: 10px">
     <el-row>
       <el-col :span="5">
         <el-form-item label="月份:"  prop="username">
           <div class="block">
             <el-date-picker
               type="month"
               size="mini"
               style="width: 150px"
               v-model="date"
               value-format="yyyy-MM"
               format="yyyy年MM月"
               placeholder="选择月">
             </el-date-picker>
           </div>
         </el-form-item>
       </el-col>
       <el-col :span="5">
         <el-form-item label="科室:"  prop="orgId">
           <el-input
             prefix-icon="el-icon-edit"
             :value="curOrgName"
             @focus="showOrgTable"
             size="mini"
             style="width: 150px"
             placeholder="选择科室"
           >
             <i slot="suffix" class="el-input__icon el-icon-circle-close" @click="clearOrgInput"></i>

           </el-input>
         </el-form-item>
       </el-col>
       <el-col :span="5">
         <el-form-item>
           <el-button size="mini" icon="el-icon-search" type="primary" @click="searchReport">查询</el-button>
         </el-form-item>
       </el-col>
     </el-row>

     <el-row>
       <el-table
         :data="orgReportData"
         border
         :show-summary='true'
         size="mini"
         style="width: 100%;">
         <el-table-column
           type="index"
           align="center"
         ></el-table-column>
         <el-table-column
           prop="date"
           align="center"
           label="日期"
           width="120px"
         >
         </el-table-column>
         <el-table-column
           prop="income"
           align="center"
           label="总收入(天/元)">
         </el-table-column>
         <el-table-column
           prop="expend"
           align="center"
           label="总支出(天/元)">
         </el-table-column>

       </el-table>
     </el-row>



   </el-form>

   <el-form  ref="OrgForm" style="margin: 0px;padding: 0px;">
     <div style="text-align: left">
       <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="EdialogVisible"  width="30%">
         <el-row>
           <el-tree
             :props="treeData"
             :load="loadChild"
             node-key="id"
             lazy
             show-checkbox
             ref="tree"
             @check="orgNodeClick"
           >
           </el-tree>
         </el-row>
         <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="orgClick">确 定</el-button>
               </span>
       </el-dialog>
     </div>
   </el-form>

 </div>
</template>

<script>
  export default {
    data(){
      return{
        orgReportData:[

        ],
        orgId:'',
        EdialogVisible:false,
        treeData:{
          children: 'children',
          label: 'label',
          id:'id',
          isLeaf:'isLeaf',
          disabled:'disabled'
        },
        dialogTitle:'',
        date:'',
        curOrgName:''



      }
    },
    methods:{

      cancelEidt(){
        this.EdialogVisible = false;
      },


      showOrgTable(){
        this.dialogTitle = "科室参照";
        this.EdialogVisible = true;
      },
      loadChild(node, resolve){
        let id=(node.parent == null?null:node.data.id);
        let resolve1 = resolve;
        this.tableLoading = true;
        this.postRequestJson('/busi/baseinfo/organization/list',{
          orgId: id,
          projectId: this.$store.state.user.projectId
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            const tdata=[];
            let result = resp.data.msg.result;

            for(let i=0;i<result.length;i++){
              tdata.push({
                id:resp.data.msg.result[i].id,
                label:resp.data.msg.result[i].name,
                children:[],
                isLeaf:resp.data.msg.result[i].isLeaf,
                disabled:!resp.data.msg.result[i].isLeaf
              });
            }
            resolve1(tdata);
          }
        });
      },

      orgClick(){

        let data = this.$refs.tree.getCheckedNodes();
        this.curOrgName=data[0].label;
        this.orgId = data[0].id;
        this.EdialogVisible = false;
      },
      orgNodeClick(data,nodes){
        console.log(data);
        console.log(nodes);
        let target = [];
        target.push(data);
        console.log(this);
        this.$refs.tree.setCheckedNodes(target,true)
      },

      clearOrgInput(){
        this.curOrgName='';
        this.orgId = '';
      },

      searchReport(){

        const loading = this.$loading({
          lock: true,
          text: '数据加载中，请等待...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });

        this.orgReportData = [];
        this.postRequestJson('/report/org',{
          orgId:this.orgId,
          date:this.date
        }).then(resp => {
          loading.close();
          if(!(resp && resp.data.code === '200')){

            this.$message({
              type: 'fail',
              message: resp.data.msg
            });
            return;
          }
          let result = resp.data.msg;
          if(result.length === 0){
            this.$message({
              type: 'fail',
              message: "该月份没有数据"
            });
            return;
          }
          for(let i = 0; i<result.length; i++){
              this.orgReportData.push({
                date:result[i].date,
                income:result[i].income,
                expend:result[i].expend
              });
          }
        });

      }




    },



  }


</script>

<style scoped>

</style>



