<template>
  <div>
    <el-form :inline="true" class="demo-form-inline" style="margin-top: 10px">
      <el-row>
        <el-col :span="5">
          <el-form-item label="月份:" prop="username">
            <div class="block">
              <el-date-picker
                type="month"
                size="mini"
                style="width: 150px"
                v-model="date"
                value-format="yyyy-MM"
                format="yyyy年MM月"
                placeholder="选择月"
                @change="changeTitle"
              >
              </el-date-picker>
            </div>
          </el-form-item>
        </el-col>
        <el-col :span="5">
          <el-form-item>
            <el-button size="mini" icon="el-icon-search" type="primary" @click="generalSalaryReport">查询</el-button>
            <el-button size="mini" icon="el-icon-upload2" type="primary" @click="exportExcelFile">导出Excel</el-button>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-table
          :data="salaryList"
          :v-loading="tableLoading"
          border
          :span-method="cellMerge"
          height="650"
          style="width: 100%;">

          <el-table-column

            :label="headerTitle"
            width="100%"
            align="center"

          >
            <el-table-column
              type="index"
              prop="index"
              align="center"
              fixed
              label="序号"
            ></el-table-column>
            <el-table-column
              prop="org"
              align="center"
              fixed
              label="科室"
            ></el-table-column>
            <el-table-column
              prop="name"
              align="center"
              fixed
              label="姓名"
            >
            </el-table-column>
            <el-table-column
              prop="job"
              align="center"
              label="职务"
              fixed
            >
            </el-table-column>
            <el-table-column
              prop="daycount"
              fixed
              align="center"
              label="计发天数">
            </el-table-column>
            <el-table-column
              prop="salary"
              label="应发工资"
              fixed
              align="center"
            >
            </el-table-column>
            <el-table-column
              prop="reward"
              align="center"
              fixed
              label="奖励"
            ></el-table-column>
            <el-table-column
              prop="total"
              align="center"
              fixed
              label="工资合计"
            ></el-table-column>


          </el-table-column>

        </el-table>
      </el-row>

    </el-form>
  </div>
</template>

<script>

  export default {
    data() {
      return {
        salaryList: [],
        mergeArr: [],
        pos: 0,
        date: '',
        headerTitle: "广州安裕物业管理有限公司驻   " + this.$store.state.user.projectName + "   员工考勤统计表",
        tableLoading: false
      };
    },
    methods: {

      cellMerge({row, column, rowIndex, columnIndex}) {
        if (columnIndex === 1) {
          const row1 = this.mergeArr[rowIndex];
          const col = row1 > 0 ? 1 : 0;
          return {
            rowspan: row1,
            colspan: col
          };
        }
      },

      generalSalaryReport() {

        const loading = this.$loading({
          lock: true,
          text: '数据加载中，请等待...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        this.tableLoading = true;
        this.postRequestJson('/report/salary', {
          projectId: 2,
          date: this.date
        }).then(resp => {
          console.log(resp);
          loading.close();
          this.salaryList = [];


          if (!(resp && resp.data.code === '200')) {
            this.$message({
              type: 'fail',
              message: resp.data.msg
            });
            return;
          }
          let result = resp.data.msg;
          if (result.length === 0) {
            this.$message({
              type: 'fail',
              message: "该月份没有数据"
            });
            return;
          }
          for (let i = 0; i < result.length; i++) {
            let org = result[i].careworkers;
            this.mergeArr.push(org.length)
            for (let j = 0; j < org.length; j++) {
              this.mergeArr.push(0);
              this.salaryList.push({
                org: result[i].orgName,
                name: org[j].name,
                job: org[j].jobName,
                daycount: org[j].countDay,
                salary: org[j].salary,
                reward: org[j].reward,
                total: org[j].reward + org[j].salary
              });
            }
            this.mergeArr.pop();
          }
          this.tableLoading = false;
        });

      },

      changeTitle() {
        this.headerTitle = "广州安裕物业管理有限公司驻   " + this.$store.state.user.projectName + "   员工" + this.date + "考勤统计表"
      },

      exportExcelFile() {
        const loading = this.$loading({
          lock: true,
          text: '数据加载中，请等待...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        this.postRequestFile('/report/salary/file', {
          projectId: 2,
          date: this.date
        }).then(resp => {
          console.log(resp);
          loading.close();
          if (resp && resp.status == 200 && resp.data.code == null) {
            let blob = new Blob([resp.data], {type: 'application/vnd.ms-excel'})
            let link = document.createElement('a')
            link.href = window.URL.createObjectURL(blob)
            //配置下载的文件名
            link.download = '陪护人员工资统计' + '_' + this.date + '.xlsx'
            link.click()
          } else if (resp.data.code != null) {
            this.$message({
              type: 'fail',
              message: resp.data.msg
            });
            return;
          }

        });


      },


    },

  }

</script>

<style scoped>

</style>



